:root {
  --background: rgba(255, 255, 255, 1);
  --cardBackground: rgba(53, 53, 53, 1);
  --textNavbar: rgba(255, 255, 255, 1);
  --text: rgba(55, 55, 55, 1);
  --cardText: rgb(189, 189, 189);
  --culoare: #ffd900;
  --timp: 500ms;
  --culoareBara: rgb(65 163 255);
  --margine: 15%;
}
body {
  border: 0;
  font-size: 100%;
  box-sizing: border-box;
  font-family: "Lato", sans-serif;
  display: block;
  cursor: default;
}
a {
  text-decoration: none;
  cursor: pointer;
}

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}
.ContainerApp {
  width: 100%;
  overflow-x: hidden;
}
.Navbar {
  position: absolute;
  background-color: var(--navbarBackground);
  color: var(--textNavbar);
  width: 100%;
  padding: 3vh var(--margine) 3vh var(--margine);
  display: flex;
  flex-direction: row;
  font-family: "Lato", sans-serif;
  align-items: center;
  text-shadow: 1px 1px 5px rgba(55, 55, 55, 1);
}

.NavbarDreapta {
  display: flex;
  flex: 1;
  flex-direction: row-reverse;
  font-size: 3vh;
}
.NavbarDreapta div {
  padding-left: 20px;
  cursor: pointer;
  transition: 0.5s;
  color: white;
}
.NavbarDreapta div:hover {
  color: var(--culoare);
}
.NavbarTitlu {
  font-size: 4.5vh;
  vertical-align: middle;
  font-weight: bold;
}
.BackgroundTop {
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
  margin: 0;
  padding: 0;
  width: 100%;
  height: 100vh;
  background-image: url("./Imagini/background.jpg");
  background-size: cover;
  font-size: 8vh;
  font-weight: 700;
  color: white;
  font-family: "Montserrat", sans-serif;
}
.BackgroundTopTransitie {
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
  margin: 0;
  padding: 0;
  width: 100%;
  height: 100vh;
}
.BackgroundTopButton {
  font-size: 3vh;
  background-color: var(--culoare);
  border-radius: 5px;
  font-family: "Lato", sans-serif;
  transition: 0.3s;
  margin-top: 5vh;
  font-weight: 400;
  padding: 3vh 6vh 3vh 6vh;
}
.BackgroundTopButton:hover {
  background-color: black;
  cursor: pointer;
}
.Despre {
  width: 100%;
  padding: 10vh var(--margine) 10vh var(--margine);
  display: flex;
  flex-direction: row;
  font-family: "Lato", sans-serif;
  font-size: 3vh;
  align-items: stretch;
}
.DespreStanga {
  flex: 1;
  padding: 10px;
  padding-left: 0;
  line-height: 180%;
}
.DespreTitluSectiune {
  font-size: 4vh;
  font-weight: bold;
  padding-bottom: 20px;
}
.DespreDreapta {
  flex: 1;
  display: flex;
  justify-content: center;
  align-items: center;
}
.DespreDreapta img {
  width: 100%;
}
.Contact {
  width: 100%;
  padding: 10vh var(--margine) 10vh var(--margine);
  display: flex;
  flex-direction: column;
  font-family: "Lato", sans-serif;
  font-size: 3vh;
  align-items: stretch;
  background-color: rgb(248, 248, 248);
}
.ContactTitlu {
  font-size: 4vh;
  font-weight: bold;
  padding-bottom: 20px;
}
.ContactSubTitlu {
  font-weight: bold;
}
.Adresa {
  display: flex;
  flex-direction: row;
  margin-top: 1vh;
}
.Adresa div {
  margin-left: 15px;
}
.Footer {
  width: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
  padding: 5vh 12vw 5vh 12vw;
  font-size: 3vh;
  background-color: var(--cardBackground);
}
.Footer a {
  color: var(--cardText);
  transition: 0.5s;
}
.Footer a:hover {
  color: rgba(255, 255, 255, 1);
}
.InformatiiNumar {
  position: relative;
  width: 100%;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  padding: 15vh 12vw 15vh 12vw;
  font-size: 3vh;
  background-attachment: fixed;
  background-repeat: no-repeat;
  background-image: url("Imagini/InformatiiNumar3.jpg");
  background-size: cover;
  font-weight: 900;
}
.InformatiiNumarCard {
  font-size: 8vh;
  color: white;
  text-shadow: 1px 1px 4px black;
  justify-content: center;
  display: flex;
  flex-direction: column;
  align-items: center;
}
.InformatiiNumarCardNumar {
  color: var(--culoare);
  font-family: "Montserrat", sans-serif;
}

.containerPortofoliuSus {
  width: 100%;
  padding: 45vh var(--margine) 5vh var(--margine);
  height: 100vh;
  background-image: url("./Imagini/background.jpg");
  background-size: cover;
  display: flex;
  flex-direction: row;
}
.containerPortofoliuSusTitlu {
  width: 100%;
  font-size: 10vh;
  color: white;
  font-weight: 900;
  text-align: center;
  font-family: "Montserrat", sans-serif;
  margin-top: -5vh;
}

.BaraDreapta {
  width: 5px;
  height: 110%;
  background-color: var(--culoareBara);
  border-radius: 10px;
  position: absolute;
  z-index: 1000;
}
.Bulina {
  width: 30px;
  height: 30px;
  background-color: var(--culoareBara);
  border-radius: 100%;
  position: absolute;
  top: 0;
  left: -13px;
}
.containerPortofoliuDate {
  margin-left: 5vw;
  font-size: 6vh;
  display: flex;
  flex-direction: column;
  color: white;
  margin-top: -2vh;
}
.ContainerProiect img {
  width: 30%;
  height: 30vh;
  margin: 10px;
  box-shadow: 0px 0px 5px 5px rgb(44, 44, 44);
}
.pozePortofoliu {
  display: flex;
  flex-wrap: wrap;
  flex-direction: row;
  margin-left: -10px;
}
.ContainerProiect {
  position: relative;
  background: rgba(53, 53, 53, 1);
  width: 100%;
  padding: 15vh var(--margine) 5vh var(--margine);
}
.ContainerProiect:last-child {
  overflow: hidden;
}
